"use client"

import type React from "react"
import { useState, useRef, useEffect } from "react"
import { Button } from "@/components/ui/button"
import { Dialog, DialogContent, DialogHeader, DialogTitle } from "@/components/ui/dialog"
import { uploadImage } from "@/app/actions/upload"
import { useToast } from "@/hooks/use-toast"
import { Upload, X } from "lucide-react"

interface ImageUploaderProps {
  value: string
  onChange: (url: string) => void
  aspectRatio?: number
}

export function ImageUploader({ value, onChange, aspectRatio = 16 / 9 }: ImageUploaderProps) {
  const [file, setFile] = useState<File | null>(null)
  const [preview, setPreview] = useState<string>("")
  const [showCropper, setShowCropper] = useState(false)
  const [uploading, setUploading] = useState(false)
  const canvasRef = useRef<HTMLCanvasElement>(null)
  const imgRef = useRef<HTMLImageElement>(null)
  const fileInputRef = useRef<HTMLInputElement>(null)
  const [cropBox, setCropBox] = useState({ x: 0, y: 0, width: 0, height: 0 })
  const { toast } = useToast()

  const handleFileSelect = (e: React.ChangeEvent<HTMLInputElement>) => {
    const selected = e.target.files?.[0]
    if (selected) {
      setFile(selected)
      const reader = new FileReader()
      reader.onload = (event) => {
        setPreview(event.target?.result as string)
        setShowCropper(true)
      }
      reader.readAsDataURL(selected)
    }
  }

  useEffect(() => {
    if (preview && imgRef.current && showCropper) {
      const img = imgRef.current
      const maxWidth = 400
      const maxHeight = 300

      const scale = Math.min(maxWidth / img.naturalWidth, maxHeight / img.naturalHeight)
      const width = img.naturalWidth * scale
      const height = img.naturalHeight * scale

      const cropWidth = Math.min(width, width / aspectRatio)
      const cropHeight = cropWidth / aspectRatio

      setCropBox({
        x: (width - cropWidth) / 2,
        y: (height - cropHeight) / 2,
        width: cropWidth,
        height: cropHeight,
      })
    }
  }, [preview, aspectRatio, showCropper])

  const handleCropConfirm = async () => {
    if (!canvasRef.current || !imgRef.current || !file) return

    const canvas = canvasRef.current
    const img = imgRef.current
    const ctx = canvas.getContext("2d")

    if (!ctx) return

    // Dynamic dimensions based on aspect ratio
    const targetWidth = aspectRatio === 1 ? 400 : 450
    const targetHeight = aspectRatio === 1 ? 400 : 350

    canvas.width = targetWidth
    canvas.height = targetHeight

    const scale = img.naturalWidth / img.width
    ctx.drawImage(
      img,
      cropBox.x * scale,
      cropBox.y * scale,
      (cropBox.width * scale * targetWidth) / canvas.width,
      (cropBox.height * scale * targetHeight) / canvas.height,
      0,
      0,
      targetWidth,
      targetHeight,
    )

    setUploading(true)
    try {
      canvas.toBlob(async (blob) => {
        if (blob) {
          const formData = new FormData()
          formData.append("file", blob)

          const url = await uploadImage(formData)
          onChange(url)
          setShowCropper(false)
          setPreview("")
          setFile(null)
          toast({
            title: "Success",
            description: "Image uploaded successfully",
          })
        }
      }, "image/jpeg")
    } catch (error) {
      toast({
        title: "Error",
        description: "Failed to upload image",
        variant: "destructive",
      })
    } finally {
      setUploading(false)
    }
  }

  return (
    <div className="space-y-4">
      <div className="flex items-center gap-2">
        <input
          ref={fileInputRef}
          type="file"
          accept="image/*"
          onChange={handleFileSelect}
          disabled={uploading}
          className="hidden"
        />
        <Button
          type="button"
          variant="outline"
          onClick={() => fileInputRef.current?.click()}
          disabled={uploading}
          className="w-full"
        >
          <Upload className="mr-2 h-4 w-4" />
          {uploading ? "Uploading..." : "Choose Image"}
        </Button>
      </div>

      {value && (
        <div className="relative h-32 w-full overflow-hidden rounded-lg border">
          <img src={value || "/placeholder.svg"} alt="Uploaded" className="h-full w-full object-cover" />
          <Button
            type="button"
            variant="destructive"
            size="icon"
            className="absolute right-2 top-2 h-6 w-6"
            onClick={() => onChange("")}
          >
            <X className="h-4 w-4" />
          </Button>
        </div>
      )}

      <Dialog open={showCropper} onOpenChange={setShowCropper}>
        <DialogContent className="max-w-lg">
          <DialogHeader>
            <DialogTitle>Crop Image</DialogTitle>
          </DialogHeader>
          <div className="space-y-4">
            <div className="relative flex justify-center">
              <img
                ref={imgRef}
                src={preview || "/placeholder.svg"}
                alt="Preview"
                className="max-h-96 max-w-full"
                style={{
                  maxWidth: "100%",
                }}
              />
              <div
                style={{
                  position: "absolute",
                  left: cropBox.x,
                  top: cropBox.y,
                  width: cropBox.width,
                  height: cropBox.height,
                  border: "2px solid #3b82f6",
                  boxSizing: "border-box",
                  cursor: "move",
                }}
                onMouseDown={(e) => {
                  const startX = e.clientX - cropBox.x
                  const startY = e.clientY - cropBox.y

                  const handleMouseMove = (moveEvent: MouseEvent) => {
                    const newX = moveEvent.clientX - startX
                    const newY = moveEvent.clientY - startY

                    setCropBox({
                      ...cropBox,
                      x: Math.max(0, Math.min(newX, imgRef.current?.width || 0 - cropBox.width)),
                      y: Math.max(0, Math.min(newY, imgRef.current?.height || 0 - cropBox.height)),
                    })
                  }

                  const handleMouseUp = () => {
                    document.removeEventListener("mousemove", handleMouseMove)
                    document.removeEventListener("mouseup", handleMouseUp)
                  }

                  document.addEventListener("mousemove", handleMouseMove)
                  document.addEventListener("mouseup", handleMouseUp)
                }}
              />
            </div>
            <canvas ref={canvasRef} className="hidden" />
            <div className="flex gap-2">
              <Button type="button" variant="outline" onClick={() => setShowCropper(false)} disabled={uploading}>
                Cancel
              </Button>
              <Button type="button" onClick={handleCropConfirm} disabled={uploading}>
                {uploading ? "Uploading..." : "Crop & Upload"}
              </Button>
            </div>
          </div>
        </DialogContent>
      </Dialog>
    </div>
  )
}
